.button {
    border-radius: 4px;
    background-color: #000000;
    border: none;
    color: #FFFFFF;
    text-align: center;
    font-size: 15px;
    padding: 20px;
    width: 350px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;
}

.button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
}

.button:hover span {
    padding-right: 25px;
}

.button:hover span:after {
    opacity: 1;
    right: 0;
}

#text_paddings {
    padding-top: 75px;
}

.a_color {
    color: whitesmoke;
    text-decoration: none;
}
.a_color:visited {
    color: #C6E7D8;
    text-decoration: none;
}
.a_color:hover {
    color: #C6E7D8;
    text-decoration: underline;
}
